import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import tmdbApi from 'api/tmdbApi/tmdbApi';
import apiConfig from 'api/apiConfig';
import { LooseObject, ItemsType } from './types';

import CastList from './castList';

import MovieList from '../../component/movie-list';

import './style.scss';

const Detail = () => {
  const { category, id } = useParams() as LooseObject;

  const [item, setItem] = useState<ItemsType>();

  useEffect(() => {
    const getDetail = async () => {
      const response = await tmdbApi.detail(category, +id, { params: {} });
      setItem(response);
      window.scrollTo(0, 0);
    };
    getDetail();
  }, [category, id]);

  return (
    <div>
      <>
        {item && (
          <>
            <div
              className="banner"
              style={{
                backgroundImage: `url(${apiConfig.originalImage(
                  item.backdrop_path || item.poster_path
                )})`,
              }}
            ></div>
            <div className="mb-3 movie-content container">
              <div className="movie-content__poster">
                <div
                  className="movie-content__poster__img"
                  style={{
                    backgroundImage: `url(${apiConfig.originalImage(
                      item.poster_path || item.backdrop_path
                    )})`,
                  }}
                ></div>
              </div>
              <div className="movie-content__info">
                <h1 className="title">{item.title || item.name}</h1>
                <div className="genres">
                  {item.genres &&
                    item.genres.slice(0, 5).map(gen => (
                      <span className="genres__item" key={gen.id}>
                        {gen.name}
                      </span>
                    ))}
                </div>
                <p className="overview">{item.overview}</p>
                <div className="cast">
                  <h2>演員陣容</h2>
                </div>
                <CastList id={item.id} />
              </div>
            </div>
            <div className="container">
              <div className="section mb-3">
                <div className="section__header mb-2">
                  <h2>相似類型電影</h2>
                </div>
                <MovieList category={category} type="similar" id={item.id} />
              </div>
            </div>
          </>
        )}
      </>
    </div>
  );
};

export default Detail;
